body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#divBody {
    margin: 1em 0.5em;
    margin-bottom: 3em;
    border: solid 1px;
    border-radius: 0.5em;
}


#divBottomNav {
    position: fixed;
    z-index: 1;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    border-top: solid 1px;
    background: #ffffffb3;
}


#divBottomNav svg {
    font-size: 2em;
}


/*#region 设置 */
#setting-app {
    margin: 2em 5vw;
}

#setting-app h5, #setting-app h3 {
    margin: 0;
}

#setting-app h5 {
    font-size: large;
    margin: 0.2em 0;
    font-weight: normal;
    color: #888;
}

.value-desc {
    position: absolute;
    border: solid 1px;
    border-radius: 3px;
    font-size: 12px;
    color: blue;
    padding: 1px 3px;
    margin: 3px 0 0 11px;
}


.setting-item-align-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.setting-item-align-right {
    display: flex;
    align-items: center;
    justify-content: end;
    padding-right: 0.5em;
}


/*#endregion*/